___CSS_LOADER_EXPORT___.push([module.id, ".preview[data-v-fd651ef6]{max-width:720px}", "",{"version":3,"sources":["webpack://./resources/js/frontend/components/social/ArticlePreview.vue"],"names":[],"mappings":"AAuyBA,0BACA,eACA","sourcesContent":["<template>\r\n  <div class=\"preview align-self-center\">\r\n    <h1 class=\"text-center\">效果預覽</h1>\r\n    <canvas class=\"rounded mx-auto d-block w-100\" ref=\"canvasView\">\r\n      <!-- 倘若使用者的瀏覽器並不支援 canvas，將會顯示該段內容。 -->\r\n      您的瀏覽器必須支援 HTML5 標籤語法，才能使用圖片(即時)預覽功能。\r\n    </canvas>\r\n    <p class=\"text-danger\">\r\n      因為字型檔(font)容量很大，所以我們決定把字型的預覽繪製當作有跟沒有之間。\r\n    </p>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: \"ArticlePreview\",\r\n  props: {\r\n    /**\r\n     * 文字內容\r\n     */\r\n    content: {\r\n      type: String,\r\n      required: true,\r\n    },\r\n    /**\r\n     * 主題\r\n     */\r\n    theme: {\r\n      type: String,\r\n      required: true,\r\n    },\r\n    /**\r\n     * 字型\r\n     */\r\n    font: {\r\n      type: String,\r\n      required: true,\r\n    },\r\n  },\r\n  data() {\r\n    return {\r\n      /**\r\n       * 畫布設定\r\n       */\r\n      canvas: {\r\n        view: null,\r\n        ctx: null,\r\n        width: 960,\r\n        height: 720,\r\n        center: false,\r\n      },\r\n      /**\r\n       * 預設值\r\n       */\r\n      default: {\r\n        canvas: {\r\n          width: 960,\r\n          height: 720,\r\n        },\r\n        line: {\r\n          height: 80,\r\n        },\r\n      },\r\n      /**\r\n       * 主題選項\r\n       */\r\n      themes: {\r\n        \"black-green\": {\r\n          background: \"#000000\",\r\n          text: \"#00FF3B\",\r\n        },\r\n        \"black-yellow\": {\r\n          background: \"#000000\",\r\n          text: \"#EBD443\",\r\n        },\r\n        \"black-white\": {\r\n          background: \"#000000\",\r\n          text: \"#F8F9FA\",\r\n        },\r\n        \"black-red\": {\r\n          background: \"#000000\",\r\n          text: \"#DC3545\",\r\n        },\r\n        \"sweet-panda\": {\r\n          background: \"#E83E8C\",\r\n          text: \"#F8F9FA\",\r\n        },\r\n        \"blue-white\": {\r\n          background: \"#007BFF\",\r\n          text: \"#F8F9FA\",\r\n        },\r\n        \"white-blue\": {\r\n          background: \"#F8F9FA\",\r\n          text: \"#007BFF\",\r\n        },\r\n        laravel: {\r\n          background: \"#F4645F\",\r\n          text: \"#F8F9FA\",\r\n        },\r\n        \"soft-green\": {\r\n          background: \"#39C5BB\",\r\n          text: \"#000000\",\r\n        },\r\n        \"grey-pikachu\": {\r\n          background: \"#2F3437\",\r\n          text: \"#FFD547\",\r\n        },\r\n        \"grey-eevee\": {\r\n          background: \"#2F3437\",\r\n          text: \"#E7AF56\",\r\n        },\r\n        \"pikachu-grey\": {\r\n          background: \"#FFD547\",\r\n          text: \"#2F3437\",\r\n        },\r\n        \"eevee-grey\": {\r\n          background: \"#E7AF56\",\r\n          text: \"#2F3437\",\r\n        },\r\n        \"chinese-new-year\": {\r\n          background: \"#A61723\",\r\n          text: \"#D8B06A\",\r\n        },\r\n        \"reverse-chinese-new-year\": {\r\n          background: \"#D8B06A\",\r\n          text: \"#A61723\",\r\n        },\r\n        devotion: {\r\n          background: \"#F11541\",\r\n          text: \"#000000\",\r\n        },\r\n        \"windows-10-error\": {\r\n          background: \"#007BD0\",\r\n          text: \"#F8F9FA\",\r\n        },\r\n        \"windows-10-error-testing\": {\r\n          background: \"#107C10\",\r\n          text: \"#F8F9FA\",\r\n        },\r\n        pink: {\r\n          background: \"#F8C0C8\",\r\n          text: \"#FF5376\",\r\n        },\r\n        \"broken-think\": {\r\n          background: \"#F8F9FA\",\r\n          text: \"#000000\",\r\n        },\r\n        \"furry-broken-think\": {\r\n          background: \"#F8F9FA\",\r\n          text: \"#000000\",\r\n        },\r\n      },\r\n      /**\r\n       * 字型選項\r\n       */\r\n      fonts: {\r\n        auraka: {\r\n          name: \"AURAKA 點陣宋字型\",\r\n          font: \"Auraka\",\r\n        },\r\n        kc24m: {\r\n          name: \"國喬點陣字型\",\r\n          font: \"KC24M\",\r\n        },\r\n        \"microsoft-jheng-hei\": {\r\n          name: \"微軟正黑體\",\r\n          font: \"Microsoft JhengHei\",\r\n        },\r\n        mingliu: {\r\n          name: \"新細明體\",\r\n          font: \"Mingliu\",\r\n        },\r\n        kaiu: {\r\n          name: \"標楷體\",\r\n          font: \"Kaiu\",\r\n        },\r\n        \"fot-matissepro-eb\": {\r\n          name: \"極粗明朝體\",\r\n          font: \"MatissePro EB\",\r\n        },\r\n        \"taipei-sans-tc-beta-bold\": {\r\n          name: \"台北黑體\",\r\n          font: \"Taipei Sans TC Beta\",\r\n        },\r\n        \"cubic-11\": {\r\n          name: \"俐方體 11 號\",\r\n          font: \"Cubic 11\",\r\n        },\r\n      },\r\n    };\r\n  },\r\n  mounted() {\r\n    this.resetCanvasView();\r\n    this.settingCanvasViewSize();\r\n    this.drawingBackground();\r\n    this.drawingBackgroundImage();\r\n    this.drawingLogo();\r\n    this.drawingUrl();\r\n    this.drawingContent();\r\n  },\r\n  methods: {\r\n    /**\r\n     * 重置 Canvas view 物件、ctx 物件\r\n     *\r\n     * @return void\r\n     */\r\n    resetCanvasView() {\r\n      this.canvas.view = this.$refs.canvasView;\r\n      this.canvas.ctx = this.canvas.view.getContext(\"2d\");\r\n    },\r\n    /**\r\n     * 設定 Canvas view 的寬度(width)、高度(height)\r\n     *\r\n     * @return void\r\n     */\r\n    settingCanvasViewSize() {\r\n      /**\r\n       * 獲得文章內容經過分割後的行數\r\n       */\r\n      let lineCount = this.contentSplit().length;\r\n      /**\r\n       * 判斷文章是否垂直置中，假設每行佔 ${default.line.height} 的高度，文章內容小於 600px 則會垂直置中對齊\r\n       */\r\n      let canvasViewCenter =\r\n        lineCount * this.default.line.height < 600 ? true : false;\r\n      /**\r\n       * 計算出 Canvas 的寬度(width)、高度(height)\r\n       * 如果文章沒有很長，那就給予預設高度 ${default.canvas.height}\r\n       * 如果文章過長，就根據其他元件的高度 + (每行 * ${default.line.height})\r\n       */\r\n      let canvasViewWidth = this.default.canvas.width;\r\n      let canvasViewHeight = canvasViewCenter\r\n        ? this.default.canvas.height\r\n        : 144 + lineCount * this.default.line.height;\r\n      /**\r\n       * 如果是選擇特殊樣式，會有其它裝飾元素，必須再額外給予更多的寬度(width)、高度(height)\r\n       */\r\n      switch (this.theme) {\r\n        // 主題: Windows 10 錯誤畫面\r\n        case \"windows-10-error\":\r\n          canvasViewHeight += 360;\r\n          break;\r\n\r\n        // 主題: Windows 10 測試人員計畫 錯誤畫面\r\n        case \"windows-10-error-testing\":\r\n          canvasViewHeight += 360;\r\n          break;\r\n\r\n        // 主題: 支離滅裂な思考・発言\r\n        case \"broken-think\":\r\n          canvasViewHeight += 160;\r\n          canvasViewWidth += 349;\r\n          break;\r\n\r\n        // 主題: 不獣控制な思考・発言\r\n        case \"furry-broken-think\":\r\n          canvasViewHeight += 160;\r\n          canvasViewWidth += 349;\r\n          break;\r\n      }\r\n      /**\r\n       * 將計算結果寫入 data() 當中\r\n       */\r\n      this.canvas.width = canvasViewWidth;\r\n      this.canvas.height = canvasViewHeight;\r\n      this.canvas.center = canvasViewCenter;\r\n      /**\r\n       * 將 canvas 物件重新設定其寬度(width)、高度(height)\r\n       */\r\n      this.canvas.view.width = this.canvas.width;\r\n      this.canvas.view.height = this.canvas.height;\r\n    },\r\n    /**\r\n     * 對畫布繪製背景顏色\r\n     *\r\n     * @return void\r\n     */\r\n    drawingBackground() {\r\n      this.canvas.ctx.fillStyle = this.themes[this.theme].background;\r\n      this.canvas.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);\r\n    },\r\n    /**\r\n     * 對畫布繪製背景圖片\r\n     *\r\n     * @return void\r\n     */\r\n    drawingBackgroundImage() {\r\n      let self = this;\r\n      switch (this.theme) {\r\n        // 主題: 慈孤觀音\r\n        case \"devotion\":\r\n          var sources = {\r\n            devotion: \"/img/frontend/cards/devotion-bg.png\",\r\n          };\r\n          this.loadingImages(sources, function (images) {\r\n            self.canvas.ctx.drawImage(images.devotion, 360, 64);\r\n            self.drawingLogo();\r\n            self.drawingUrl();\r\n          });\r\n          break;\r\n\r\n        // 主題: Windows 10 錯誤畫面\r\n        case \"windows-10-error\":\r\n          var sources = {\r\n            qrcode: \"/img/frontend/cards/qrcode.png\",\r\n          };\r\n          this.loadingImages(sources, function (images) {\r\n            self.canvas.ctx.drawImage(\r\n              images.qrcode,\r\n              24,\r\n              self.canvas.height - 204\r\n            );\r\n            self.drawingLogo();\r\n            self.drawingUrl();\r\n          });\r\n          break;\r\n\r\n        // 主題: Windows 10 測試人員計畫 錯誤畫面\r\n        case \"windows-10-error-testing\":\r\n          var sources = {\r\n            qrcode: \"/img/frontend/cards/qrcode.png\",\r\n          };\r\n          this.loadingImages(sources, function (images) {\r\n            self.canvas.ctx.drawImage(\r\n              images.qrcode,\r\n              24,\r\n              self.canvas.height - 204\r\n            );\r\n            self.drawingLogo();\r\n            self.drawingUrl();\r\n          });\r\n          break;\r\n\r\n        // 主題: 支離滅裂な思考・発言\r\n        case \"broken-think\":\r\n          /**\r\n           * 定義圖片資源\r\n           * background => 背景的紫色圓圈\r\n           * people => 支離破碎的人物\r\n           * arrow => 對話框的箭頭\r\n           */\r\n          var sources = {\r\n            background: \"/img/frontend/cards/fragmented_background.png\",\r\n            people: \"/img/frontend/cards/fragmented_people.png\",\r\n            arrow: \"/img/frontend/cards/fragmented_background_arrow.png\",\r\n          };\r\n          this.loadingImages(sources, function (images) {\r\n            /**\r\n             * 繪製背景圓圈與人物\r\n             */\r\n            self.canvas.ctx.drawImage(\r\n              images.background,\r\n              0,\r\n              self.canvas.height - 560\r\n            );\r\n            self.canvas.ctx.drawImage(\r\n              images.people,\r\n              36,\r\n              self.canvas.height - 542\r\n            );\r\n\r\n            /**\r\n             * 繪製對話框\r\n             */\r\n            self.canvas.ctx.lineJoin = \"round\";\r\n            self.canvas.ctx.lineWidth = 8;\r\n            self.canvas.ctx.strokeRect(\r\n              353,\r\n              40,\r\n              self.canvas.width - 381,\r\n              self.canvas.height - 282\r\n            );\r\n            self.canvas.ctx.fillStyle = \"#FFFFFF\";\r\n            self.canvas.ctx.fillRect(\r\n              357,\r\n              44,\r\n              self.canvas.width - 389,\r\n              self.canvas.height - 290\r\n            );\r\n\r\n            /**\r\n             * 繪製對話框的箭頭\r\n             */\r\n            self.canvas.ctx.drawImage(\r\n              images.arrow,\r\n              312,\r\n              self.canvas.height - 388\r\n            );\r\n            self.drawingLogo();\r\n            self.drawingUrl();\r\n            self.drawingContent();\r\n          });\r\n          break;\r\n\r\n        // 主題: 不獣控制な思考・発言\r\n        case \"furry-broken-think\":\r\n          /**\r\n           * 定義圖片資源\r\n           * background => 背景的紫色圓圈\r\n           * wolf => 支離破碎的狼\r\n           * arrow => 對話框的箭頭\r\n           */\r\n          var sources = {\r\n            background: \"/img/frontend/cards/fragmented_background.png\",\r\n            wolf: \"/img/frontend/cards/fragmented_wolf.png\",\r\n            arrow: \"/img/frontend/cards/fragmented_background_arrow.png\",\r\n          };\r\n          this.loadingImages(sources, function (images) {\r\n            /**\r\n             * 繪製背景圓圈與狼\r\n             */\r\n            self.canvas.ctx.drawImage(\r\n              images.background,\r\n              0,\r\n              self.canvas.height - 560\r\n            );\r\n            self.canvas.ctx.drawImage(\r\n              images.wolf,\r\n              12,\r\n              self.canvas.height - 482\r\n            );\r\n\r\n            /**\r\n             * 繪製對話框\r\n             */\r\n            self.canvas.ctx.lineJoin = \"round\";\r\n            self.canvas.ctx.lineWidth = 8;\r\n            self.canvas.ctx.strokeRect(\r\n              353,\r\n              40,\r\n              self.canvas.width - 381,\r\n              self.canvas.height - 282\r\n            );\r\n            self.canvas.ctx.fillStyle = \"#FFFFFF\";\r\n            self.canvas.ctx.fillRect(\r\n              357,\r\n              44,\r\n              self.canvas.width - 389,\r\n              self.canvas.height - 290\r\n            );\r\n\r\n            /**\r\n             * 繪製對話框的箭頭\r\n             */\r\n            self.canvas.ctx.drawImage(\r\n              images.arrow,\r\n              312,\r\n              self.canvas.height - 388\r\n            );\r\n            self.drawingLogo();\r\n            self.drawingUrl();\r\n            self.drawingContent();\r\n            // self.drawingFeature();\r\n          });\r\n          break;\r\n      }\r\n    },\r\n    /**\r\n     * 對畫布繪製 LOGO\r\n     *\r\n     * @return void\r\n     */\r\n    drawingLogo() {\r\n      switch (this.theme) {\r\n        // 主題: Windows 10 錯誤畫面\r\n        case \"windows-10-error\":\r\n          this.canvas.ctx.font = \"36px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\r\n            \"若要深入了解，您稍候可以線上搜尋此:\",\r\n            228,\r\n            this.canvas.height - 160\r\n          );\r\n          this.canvas.ctx.fillText(\r\n            \"純靠北工程師 0xINIT_ENGINEER\",\r\n            228,\r\n            this.canvas.height - 120\r\n          );\r\n          this.canvas.ctx.fillText(\r\n            \"請訪問 https://init.engineer\",\r\n            228,\r\n            this.canvas.height - 40\r\n          );\r\n          break;\r\n\r\n        // 主題: Windows 10 測試人員計畫 錯誤畫面\r\n        case \"windows-10-error-testing\":\r\n          this.canvas.ctx.font = \"36px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\r\n            \"若要深入了解，您稍候可以線上搜尋此:\",\r\n            228,\r\n            this.canvas.height - 160\r\n          );\r\n          this.canvas.ctx.fillText(\r\n            \"純靠北工程師 0xINIT_ENGINEER\",\r\n            228,\r\n            this.canvas.height - 120\r\n          );\r\n          this.canvas.ctx.fillText(\r\n            \"請訪問 https://init.engineer\",\r\n            228,\r\n            this.canvas.height - 40\r\n          );\r\n          break;\r\n\r\n        // 主題: 支離滅裂な思考・発言\r\n        case \"broken-think\":\r\n          this.canvas.ctx.font = \"72px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\"支離滅裂な\", 360, this.canvas.height - 160);\r\n          this.canvas.ctx.fillText(\"思考・発言\", 360, this.canvas.height - 80);\r\n          this.canvas.ctx.font = \"36px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\r\n            \"純靠北工程師\",\r\n            this.canvas.width - 232,\r\n            this.canvas.height - 24\r\n          );\r\n          break;\r\n\r\n        // 主題: 不獣控制な思考・発言\r\n        case \"furry-broken-think\":\r\n          this.canvas.ctx.font = \"72px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\"不獣控制な\", 360, this.canvas.height - 160);\r\n          this.canvas.ctx.fillText(\"思考・発言\", 360, this.canvas.height - 80);\r\n          this.canvas.ctx.font = \"36px \" + this.canvas.font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\r\n            \"純靠北工程師\",\r\n            this.canvas.width - 232,\r\n            this.canvas.height - 24\r\n          );\r\n          break;\r\n\r\n        // 預設，會在右下角印上 LOGO 字樣\r\n        default:\r\n          this.canvas.ctx.font = \"36px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\r\n            \"純靠北工程師\",\r\n            this.canvas.width - 232,\r\n            this.canvas.height - 24\r\n          );\r\n          break;\r\n      }\r\n    },\r\n    /**\r\n     * 對畫布繪製網站連結\r\n     *\r\n     * @return void\r\n     */\r\n    drawingUrl() {\r\n      switch (this.theme) {\r\n        // 主題: Windows 10 錯誤畫面\r\n        case \"windows-10-error\":\r\n          this.canvas.ctx.font = \"192px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\":(\", 48, 192);\r\n          break;\r\n\r\n        // 主題: Windows 10 測試人員計畫 錯誤畫面\r\n        case \"windows-10-error-testing\":\r\n          this.canvas.ctx.font = \"192px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\":(\", 48, 192);\r\n          break;\r\n\r\n        // 預設，會在左下角印上網站連結宣傳\r\n        default:\r\n          this.canvas.ctx.font = \"36px \" + this.fonts[this.font].font;\r\n          this.canvas.ctx.fillStyle = this.themes[this.theme].text;\r\n          this.canvas.ctx.fillText(\r\n            \"發文傳送門 https://init.engineer\",\r\n            16,\r\n            this.canvas.height - 24\r\n          );\r\n          break;\r\n      }\r\n    },\r\n    /**\r\n     * 對畫布繪製文字內容\r\n     *\r\n     * @return void\r\n     */\r\n    drawingContent() {\r\n      /**\r\n       * 先取得整理過後的內容字串 array[string]\r\n       * 並且透過 forEach 的方式逐一繪製內容\r\n       */\r\n      let contentList = this.contentSplit();\r\n      contentList.forEach(\r\n        function (contentValue, contentKey) {\r\n   