es === 'vote' || states === 'voting'\"\r\n      >\r\n        <div v-if=\"voting !== 'no'\">否決</div>\r\n        <div v-else>\r\n          <span\r\n            class=\"spinner-border spinner-border-sm\"\r\n            role=\"status\"\r\n            aria-hidden=\"true\"\r\n          ></span>\r\n          <span class=\"sr-only\">Loading...</span>\r\n        </div>\r\n      </button>\r\n\r\n      <!-- 管理員的選擇 -->\r\n      <div class=\"clearfix\" v-if=\"states === 'complete'\">\r\n        <p\r\n          class=\"float-left mb-0\"\r\n          style=\"color: rgb(26, 188, 156)\"\r\n          v-show=\"voting === 'yes'\"\r\n        >\r\n          您投了同意票\r\n        </p>\r\n        <p\r\n          class=\"float-right mb-0\"\r\n          style=\"color: rgb(216, 73, 90)\"\r\n          v-show=\"voting === 'no'\"\r\n        >\r\n          您投了反對票\r\n        </p>\r\n      </div>\r\n\r\n      <!-- 投票結果 -->\r\n      <div\r\n        class=\"progress\"\r\n        style=\"position: relative; width: 130px; height: 24px\"\r\n      >\r\n        <div\r\n          class=\"progress-bar progress-bar-striped progress-bar-animated\"\r\n          role=\"progressbar\"\r\n          :style=\"\r\n            'width: ' +\r\n            ((count.yes / (count.yes + count.no)) * 100).toFixed(2) +\r\n            '%; border-style: solid none solid solid; background-color: rgb(26, 188, 156);'\r\n          \"\r\n          :aria-valuenow=\"count.yes\"\r\n          :aria-valuemin=\"0\"\r\n          :aria-valuemax=\"count.yes + count.no\"\r\n        >\r\n          {{ ((count.yes / (count.yes + count.no)) * 100).toFixed(2) }}%\r\n        </div>\r\n        <div\r\n          class=\"progress-bar progress-bar-striped progress-bar-animated\"\r\n          role=\"progressbar\"\r\n          :style=\"\r\n            'width: ' +\r\n            ((count.no / (count.yes + count.no)) * 100).toFixed(2) +\r\n            '%; border-style: solid solid solid none; background-color: rgb(216, 73, 90);'\r\n          \"\r\n          :aria-valuenow=\"count.no\"\r\n          :aria-valuemin=\"0\"\r\n          :aria-valuemax=\"count.yes + count.no\"\r\n        >\r\n          {{ ((count.no / (count.yes + count.no)) * 100).toFixed(2) }}%\r\n        </div>\r\n      </div>\r\n      <div class=\"clearfix\">\r\n        <p class=\"float-left\" style=\"color: rgb(26, 188, 156)\">\r\n          {{ count.yes }}\r\n        </p>\r\n        <p class=\"float-right\" style=\"color: rgb(216, 73, 90)\">\r\n          {{ count.no }}\r\n        </p>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: \"ReviewButton\",\r\n  props: {\r\n    cid: {\r\n      type: Number,\r\n      required: true,\r\n    },\r\n  },\r\n  data() {\r\n    return {\r\n      states: \"loading\",\r\n      voting: null,\r\n      count: {\r\n        yes: 0,\r\n        no: 0,\r\n      },\r\n    };\r\n  },\r\n  mounted() {\r\n    /**\r\n     * 抓取該篇貼文的通過、否決資訊\r\n     */\r\n    let self = this;\r\n    axios\r\n      .get(`/api/social/cards/${this.cid}/voted`)\r\n      .then(function (response) {\r\n        /**\r\n         * 先寫入通過、否決票數資訊\r\n         */\r\n        self.count.yes = response.data.count.yes;\r\n        self.count.no = response.data.count.no;\r\n\r\n        /**\r\n         * 判斷是否已經投票\r\n         */\r\n        if (response.data.voted) {\r\n          self.voting = response.data.selector ? \"yes\" : \"no\";\r\n          self.states = \"complete\";\r\n        } else {\r\n          self.states = \"vote\";\r\n        }\r\n      })\r\n      .catch(function (error) {\r\n        /**\r\n         * 處理不明的例外錯誤\r\n         */\r\n        self.states = \"error\";\r\n        Swal.fire(\r\n          \"噢噗！怪怪的？\",\r\n          '載入投票資訊時發生了一些錯誤，可以的話，把這項問題拿到<a href=\"https://github.com/init-engineer/init.engineer\"> GitHub repo </a>發個 issue 給我，謝謝你 m(_ _)m',\r\n          \"error\"\r\n        );\r\n      });\r\n  },\r\n  methods: {\r\n    /**\r\n     * 投票事件，通過\r\n     *\r\n     * @return void\r\n     */\r\n    yesVoting() {\r\n      this.states = \"voting\";\r\n      this.voting = \"yes\";\r\n\r\n      let self = this;\r\n      axios\r\n        .get(`/api/social/cards/${this.cid}/voting/1`)\r\n        .then(function (response) {\r\n          self.count.yes = response.data.count.yes;\r\n          self.count.no = response.data.count.no;\r\n          self.states = \"complete\";\r\n        })\r\n        .catch(function (error) {\r\n          /**\r\n           * 處理不明的例外錯誤\r\n           */\r\n          self.states = \"vote\";\r\n          Swal.fire(\r\n            \"噢噗！怪怪的？\",\r\n            '投票時發生了一些錯誤，可以的話，把這項問題拿到<a href=\"https://github.com/init-engineer/init.engineer\"> GitHub repo </a>發個 issue 給我，謝謝你 m(_ _)m',\r\n            \"error\"\r\n          );\r\n        });\r\n    },\r\n    /**\r\n     * 投票事件，否決\r\n     *\r\n     * @return void\r\n     */\r\n    noVoting() {\r\n      this.states = \"voting\";\r\n      this.voting = \"no\";\r\n\r\n      let self = this;\r\n      axios\r\n        .get(`/api/social/cards/${this.cid}/voting/0`)\r\n        .then(function (response) {\r\n          self.count.yes = response.data.count.yes;\r\n          self.count.no = response.data.count.no;\r\n          self.states = \"complete\";\r\n        })\r\n        .catch(function (error) {\r\n          /**\r\n           * 處理不明的例外錯誤\r\n           */\r\n          self.states = \"vote\";\r\n          Swal.fire(\r\n            \"噢噗！怪怪的？\",\r\n            '投票時發生了一些錯誤，可以的話，把這項問題拿到<a href=\"https://github.com/init-engineer/init.engineer\"> GitHub repo </a>發個 issue 給我，謝謝你 m(_ _)m',\r\n            \"error\"\r\n          );\r\n        });\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.btn {\r\n  display: inline-block;\r\n  margin: 0px;\r\n  padding: 2px;\r\n  width: 60px;\r\n  height: 48px;\r\n  border-radius: 4px;\r\n  color: #ffffff;\r\n  position: relative;\r\n  cursor: pointer;\r\n}\r\n.btn.yes {\r\n  background-color: rgb(26, 188, 156);\r\n  margin-right: 6px;\r\n}\r\n.btn.yes:hover {\r\n  background-color: rgb(29, 200, 166);\r\n}\r\n.btn.no {\r\n  background-color: rgb(216, 73, 90);\r\n}\r\n.btn.no:hover {\r\n  background-color: rgb(231, 79, 97);\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
